* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Solis', 'Arial', 'Helvetica', sans-serif;

}

.app {
  background-image: url('./assets/cover.jpg');
  background-size: cover;
  background-position: bottom;
  transition: 0.4 ease;
}
.app_top-logo {
  width: 20px;
  height:0px;
  padding-top: 5px;
  margin-left: 6rem;
  margin-top: 0rem;
  cursor: pointer

}
.menu {
  text-transform: capitalize;
  cursor: pointer;    
  justify-content: right;
  display: flex;
  padding-top: 20px;
  margin-right: 6rem;
  
}
.menu-content {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: space-between;
    background: #ffffff;
    color: black;

    padding: 2rem;
    position: absolute;
    right: 20px;
    margin-top: 2rem;
    font-size: 30px;
    min-width: 150px;
    border-radius: 5px; 
}

.app.warm {
  background-image: url('./assets/warm.jpg');
}

main {
  min-height: 100vh;
  padding: 20px;
}

.search-box {
  width: 700px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
  margin-top: -1.5rem;
  background: #ffffff;
  opacity: 0.7;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
}

.search-box .search-bar {
  width: 100%;
  padding: 10px;
  border-radius: 10px 10px 10px 10px ;
  border: none;
  outline: none;
  margin-top: -25px;
  color: #313131;
  font-size: 20px;
  transition: 0.4s ease;
  -webkit-border-radius: 10px 10px 10px 10px ;
  -moz-border-radius: 10px 10px 10px 10px ;
  -ms-border-radius: 10px 10px 10px 10px ;
  -o-border-radius: 10px 10px 10px 10px ;
}
.search-box .search-bar:focus {
  background-color: rgba(255, 255, 255, 0.75);
}


.location-box .location {
  color: #FFF;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
  margin-top: 6rem;
  padding: 10px;
}

.location-box .date {
  color: #FFF;
  font-size: 20px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
}

.weather-box {
  text-align: center;
}

.weather-box .temp {
  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 16px;

  padding: 15px 25px;
  color: #FFF;
  font-size: 102px;
  font-weight: 900;

  text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
  text-align: center;
  box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
}

.weather-box .weather {
  color: #FFF;
  font-size: 48px;
  font-weight: 700;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}





@media screen and (max-width: 550px) {
  .app{
    margin: 0px;
    padding: 0px;
  }
  .app_top-logo{
    margin-left: 2rem;
    margin-top: 0rem;
  }
  .menu {
    margin-right: 2rem;
  }
  .main{
    min-height: 100vh;
    padding: 25px;
  }

  .search-box {
  width: 300px;
  margin-top: 3rem;
  }


  .location-box .location {
  font-size: 28px;
  margin-top: 4rem;
  }

  .location-box .date {
  font-size: 20px;
  font-weight: 300;
  }

  .weather-box {
  text-align: center;
  }

  .weather-box .temp {
  margin: 25px auto;
  padding: 10px 20px;
  font-size: 62px;
}

.weather-box .weather {
  color: #FFF;
  font-size: 32px;
  font-weight: 700;
  }
}